*{
	margin:0;
	padding:0;

	font-size:16px;
	-webkit-user-select:none;
	   -moz-user-select:none;
	    -ms-user-select:none;
	        user-select:none;
}

ul{
	list-style:none;
}

select,input{
	-webkit-box-sizing:border-box;
	        box-sizing:border-box;
}

a{
	text-decoration:none;
}


#app {
	height: 100%;
}

.UI {
	background-color: #a5bad0;
}




